<template>
  <div class="list" ref="list">
    <div>
      <div class="area">
        <div class="title border-topbottom">当前城市</div>
        <div class="button-list">
          <div class="button-wrapper">
            <div class="button">{{this.currentCity}}</div>
          </div>
        </div>
      </div>
      <div class="area">
        <div class="title border-topbottom">热门城市</div>
        <div class="button-list">
          <div
            class="button-wrapper"
            v-for="item of hot"
            :key="item.id"
            @click="handleCityClick(item.name)"
          >
            <div class="button">{{item.name}}</div>
          </div>
        </div>
      </div>
      <div class="area" v-for="(item, key) of cities" :key="key">
        <div class="title border-topbottom" :ref="key">{{key}}</div>
        <div class="button-list">
          <div class="button-wrapper" v-for="city of item" :key="city.id"  @click="handleCityClick(city.name)">
            <div class="button">{{city.name}}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapState, mapActions } from 'vuex'
export default {
  name: 'CityList',
  props: {
    cities: Object,
    hot: Array,
    letter: String
  },
  computed: {
    ...mapState({
      currentCity: 'city'
    })
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push({ path: '/' })
    },
    ...mapActions(['changeCity'])
  },
  watch: {
    letter () {
      if (this.letter) {
        const ele = this.$refs[this.letter][0]
        this.scroll.scrollToElement(ele)
      }
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.list, {click: true})
  }
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/varibles.styl'
  @import '~@/assets/styles/mixins.styl'
  .list
    overflow hidden
    position absolute
    top $headerHeight + 0.84rem
    bottom 0
    left 0
    right 0
    .title
      background-color #f5f5f5
      font-size 0.26rem
      padding 0.24rem 0.3rem
      color #212121
    .button-list
      overflow hidden
      font-size 0.28rem
      padding 0.1rem 0.6rem 0.1rem 0.1rem
      .button-wrapper
        width 33.33%
        float left
        .button
          text-align center
          height 0.38rem
          line-height 0.38rem
          margin 0.1rem
          padding 0.1rem
          border 0.02rem solid $bgColor
          border-radius 0.1rem
          ellipsis()
</style>



