<template>
  <div class="header">
    <router-link to="/">
      <div class="iconfont header-back">&#xe624;</div>
    </router-link>
    <h1 class="header-title">城市选择</h1>
  </div>
</template>

<script>
export default {
  name: 'CityHeader'
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/varibles.styl'
  @import '~@/assets/styles/mixins.styl'
  .header
    overflow hidden
    position relative
    height $headerHeight
    line-height $headerHeight
    background-color $bgColor
    color #ffffff
    .header-back
      left 0
      top 0
      width $headerHeight
      height $headerHeight
      position absolute
      font-size 0.36rem
      text-align center
      color #ffffff
    .header-title
      margin 0 1rem
      font-size .34rem
      text-align center
      ellipsis()
</style>



