<template>
  <div>
    <div class="search">
      <input
        class="search-input"
        type="text"
        placeholder="输入城市名或拼音"
        maxlength="18"
        v-model="keyworld"
      >
    </div>
    <div class="content" ref="content" v-show="keyworld">
      <ul class="content-wrapper">
        <div v-show="hasNoData" class="content-info">没有找到该地点</div>
        <li class="item-wrapper" v-for="item of list" :key="item.id" @click="handleCityClick(item.name)">
          <div class="item">{{item.name}}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Bscroll from 'better-scroll'
import { mapActions } from 'vuex'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyworld: '',
      list: [],
      timer: null
    }
  },
  computed: {
    hasNoData () {
      return !this.list.length
    }
  },
  methods: {
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push({ path: '/' })
    },
    ...mapActions(['changeCity'])
  },
  watch: {
    keyworld () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      if (!this.keyworld) {
        this.list = []
        return
      }
      this.timer = setTimeout(() => {
        const res = []
        for (let i in this.cities) {
          this.cities[i].forEach(city => {
            if (city.spell.indexOf(this.keyworld) > -1 || city.name.indexOf(this.keyworld) > -1) {
              res.push(city)
            }
          })
        }
        this.list = res
      }, 100)
    }
  },
  mounted () {
    this.scroll = new Bscroll(this.$refs.content, {click: true})
  }
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/varibles.styl'
  @import '~@/assets/styles/mixins.styl'
  .search
    overflow hidden
    height 0.6rem
    padding 0.12rem
    background-color $bgColor
    .search-input
      width 100%
      height 0.6rem
      line-height 0.6rem
      text-align center
      border-radius 0.1rem
      color #666666
  .content
    overflow hidden
    position absolute
    top $headerHeight + 0.84rem
    bottom 0
    left 0
    right 0
    z-index 1
    background-color #ffffff
    .content-wrapper
      &:after
        display block
        content ''
        clear both
      .content-info
        height 0.3rem
        line-height 0.3rem
        margin 0.2rem 0.3rem
      .item-wrapper
        width 33.33%
        float left
        .item
          text-align center
          height 0.38rem
          line-height 0.38rem
          margin 0.1rem
          padding 0.1rem
          border 0.02rem solid $bgColor
          border-radius 0.1rem
          ellipsis()
</style>



