<template>
  <div class="weekend">
    <div class="title">周末去哪</div>

    <ul>
      <li class="item" v-for="item of list" :key="item.id">
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" :alt="item.title" />
        </div>
        <div class="item-info-wrapper">
          <div class="item-info-title">{{item.title}}</div>
          <div class="item-info-desc">{{item.desc}}</div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/mixins.styl'
  .weekend
    background-color #f5f5f5
    .title
      line-height 0.8rem
      height 0.8rem
      background-color #f5f5f5
      text-indent 0.2rem
    .item
      overflow hidden
      margin-bottom: 0.1rem;
      background: #ffffff;
      .item-img-wrapper
        width 100%
        height 0
        padding-bottom 37.09%
        overflow hidden
        .item-img
          width 100%
      .item-info-wrapper
        padding 0.14rem 0.2rem 0.2rem 0.2rem
        .item-info-title
          padding-right 1.4rem
          height 0.48rem
          line-height 0.48rem
          font-size 0.28rem
          color #212121
          ellipsis()
        .item-info-desc
          padding-right 1.4rem
          height 0.42rem
          line-height 0.42rem
          font-size 0.24rem
          color #616161
          ellipsis()
</style>



