<template>
  <div class="banner">
    <div class="img-wrapper" @click="handleShowGallary">
      <img :src="bannerImg" :alt="sightName">
    </div>
    <div class="img-info">
      <div class="title">{{sightName}}</div>
      <div class="sum">
        <span class="iconfont img-icon">&#xe622;</span>
        {{imgSum}}
      </div>
    </div>
    <common-fade>
      <common-gallary :imgs="gallaryImgs" v-show="showGallary" @closeGallary="handleCloseGallary"></common-gallary>
    </common-fade>
  </div>
</template>

<script>
import CommonGallary from '@/common/gallary/Gallary'
import CommonFade from '@/common/animation/Fade'
export default {
  name: 'DetailBanner',
  props: {
    sightName: String,
    bannerImg: String,
    gallaryImgs: Array
  },
  data () {
    return {
      showGallary: false
    }
  },
  components: {
    CommonGallary,
    CommonFade
  },
  computed: {
    imgSum () {
      return this.gallaryImgs.length
    }
  },
  methods: {
    handleShowGallary () {
      this.showGallary = true
    },
    handleCloseGallary () {
      this.showGallary = false
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position relative
  .img-wrapper
    overflow hidden
    width 100%
    height 0
    padding-bottom 55%
    img
      width 100%
  .img-info
    position absolute
    width 100%
    height 0.48rem
    line-height 0.48rem
    display flex
    bottom 0
    background-color #ffffff30
    color #ffffff
    .title
      flex 1
      font-size 0.36rem
      margin-left 0.4rem
    .sum
      min-width 1.2rem
</style>



