<template>
  <div class="recommend">
    <div class="title">热销推荐</div>
    <ul>
      <router-link
      tag="li"
        class="item border-bottom"
        v-for="item of list"
        :key="item.id"
        :to="'/detail_' + item.id"
      >
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" :alt="item.title" />
        </div>
        <div class="item-info-wrapper">
          <div class="item-info-title">{{item.title}}</div>
          <div class="item-info-desc">{{item.desc}}</div>
          <button class="item-info-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: Array
  }
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/mixins.styl'
  .title
    margin-top 0.2rem
    line-height 0.8rem
    height 0.8rem
    background-color #eeeeee
    text-indent 0.2rem
  .item
    display flex
    overflow hidden
    height 1.9rem
    .item-img-wrapper
      width 1.7rem
      height 1.7rem
      padding 0.1rem
      .item-img
        width 100%
    .item-info-wrapper
      flex 1
      padding 0.1rem
      min-width 0
      .item-info-title
        height 0.54rem
        line-height 0.54rem
        font-size 0.32rem
        ellipsis()
      .item-info-desc
        height 0.4rem
        line-height 0.4rem
        color #ccc
        ellipsis()
      .item-info-button
        margin-top 0.144rem
        background-color #ff9300
        padding 0.06rem
        border-radius 0.06rem
</style>



