<template>
    <div class="header">
        <a class="header-left">
          <div class="iconfont back-icon">&#xe624;</div>
        </a>
        <div class="header-input">
          <span class="iconfont search-icon">&#xe632;</span>
          输入城市查询
        </div>
        <router-link to="/city">
          <div class="header-right">
            {{this.city}}
            <span class="iconfont arrow-icon">&#xe600;</span>
          </div>
        </router-link>
    </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/styles/varibles.styl';
  .header
    display flex
    height $headerHeight
    background-color $bgColor
    color #ffffff
    text-align center
    line-height $headerHeight
    .header-left
      color #ffffff
      .back-icon
        width $headerHeight
        height $headerHeight
        font-size 0.36rem
        flex-grow 0
    .header-input
      flex-grow 1
      flex-shrink 1
      height 0.64rem
      margin-top 0.1rem
      margin-left 0.1rem
      padding-left 0.2rem
      background-color #ffffff
      border-radius 0.1rem
      line-height 0.64rem
      text-align left
      vertical-align bottom
      color #999999
      .search-icon
        font-size 0.36rem
    .header-right
      box-sizing border-box
      padding 0 0.1rem
      min-width 1.1rem
      color #ffffff
      .arrow-icon
        font-size 0.26rem
</style>



