<template>
  <div class="header">
    <router-link tag="div" to="/" class="header-abs" v-show="showAbs">
      <div class="iconfont back-icon">&#xe624;</div>
    </router-link>
    <div class="header-fixed" v-show="!showAbs" :style="opacityStyle">
      <router-link to="/">
        <div class="iconfont header-back">&#xe624;</div>
      </router-link>
      <h1 class="header-title">景点详情</h1>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  data () {
    return {
      showAbs: true,
      opacityStyle: {
        opacity: 0
      }
    }
  },
  methods: {
    handleScroll () {
      const top = document.documentElement.scrollTop
      if (top > 60) {
        if (top < 120) {
          let opacity = (top - 60) / 60
          this.opacityStyle = {opacity}
        }
        this.showAbs = false
      } else {
        this.showAbs = true
      }
    }
  },
  activated () {
    window.addEventListener('scroll', this.handleScroll)
  },
  deactivated () {
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~@/assets/styles/varibles.styl'
  .header-abs
    position absolute
    top .1rem
    left .1rem
    width .72rem
    height .72rem
    border-radius .36rem
    background-color #000
    opacity .5
    color #fff
    .back-icon
      position absolute
      left 0
      top 0
      height .72rem
      width .66rem
      line-height .72rem
      text-align center
      font-weight bold
  .header-fixed
    position fixed
    top 0
    left 0
    right 0
    overflow hidden
    height $headerHeight
    line-height $headerHeight
    background-color $bgColor
    color #ffffff
    z-index 10
    .header-back
      left 0
      top 0
      width $headerHeight
      height $headerHeight
      position absolute
      font-size 0.36rem
      text-align center
      color #ffffff
    .header-title
      margin 0 1rem
      font-size .34rem
      text-align center
</style>



